@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;500&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{font-family:'Roboto Mono', monospace, Arial, sans-serif;background-color:#434343;display:flex;justify-content:center;align-items:center;height:100vh}@media screen and (max-width: 750px){html{font-size:50%}}

.panel{position:relative;background-color:#7d7d7f;width:480px;border-radius:5px;border:1px solid black;box-shadow:0 0 5px black;padding:3.5rem}@media screen and (max-width: 1024px){.panel{width:510px;padding:4.5rem}}@media screen and (max-width: 750px){.panel{width:380px}}@media screen and (max-width: 400px){.panel{width:300px}}

.panel-screen{border-radius:5px;height:20vmin;border:2px solid rgba(0,0,0,0.7);position:relative;color:#434343}.panel-screen--light-on{background-color:#7fffff}.panel-screen--light-off{background-color:#47b2b2}@media screen and (max-width: 750px){.panel-screen{height:30vmin}}

.panel-screen__status{font-size:1.4rem;position:absolute;top:1rem;left:.6rem}

.panel-screen__message{font-size:2.5rem;font-weight:500;letter-spacing:1.5;position:absolute;bottom:1.3rem;right:1.9rem}

.panel-button{justify-self:center;height:100px;width:100px;border-radius:5px;background-color:#63636e;border:1px solid black;box-shadow:2px 2px 4px rgba(0,0,0,0.8);font-size:2.5rem;font-weight:500;color:#f3f3f3;outline:none;cursor:pointer;font-family:'Roboto Mono'}.panel-button:active{transform:translateY(2px);box-shadow:0 0 0 transparent}@media screen and (max-width: 750px){.panel-button{height:50px;width:50px}}@media screen and (max-width: 550px){.panel-button{height:70px;width:70px}}@media screen and (max-width: 400px){.panel-button{height:60px;width:60px}}

.panel-buttons{display:grid;padding:30px 10px;grid-template-columns:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:20px}

.serial-number{font-size:.8rem;position:absolute;bottom:5px;right:5px}

